<template>
  <!--  -->
  <div id="home">
    <!--  -->
    <search></search>
    <!--  -->
    <div class="swipewrap">
      <swipe></swipe>
      <!--  -->
      <div class="cover"></div>
    </div>
    <!--  -->
    <gird></gird>
    <!--  -->
    <recommend class="recommend"></recommend>
    <!--  -->
    <firstnew></firstnew>
    <!--  -->
    <div class="youprefer">
      <img class="youpreferimg" src="@a/icon/youprefer.png" alt />
    </div>
    <!--  -->
    <div class="goodslistwrap">
      <goodslist ref="goodslist"></goodslist>
    </div>
    <!--  -->
    <backtop class></backtop>
    <!-- Tabbar -->
    <tabbar></tabbar>
    <scroll ref="scroll"></scroll>
    <goodsinfo v-if="false"></goodsinfo>
  </div>
</template> 

<script>
import backtop from "../components/common/backtop";
import firstnew from "../components/common/firstnew";
import search from "../components/common/search";
import swipe from "../components/common/Swipe";
import tabbar from "../components/common/Tabbar";
import gird from "../components/common/Grid";
import recommend from "../components/common/recommend";
import goodslist from "../components/common/goodslist";
import scroll from "../components/common/Scroll";
import goodsinfo from "../components/common/goodsinfo";

// Vantmousetouch
import "@vant/touch-emulator";
export default {
  name: "App",
  data() {
    return {
      currentpage: 1, // 
      pageaddress: "http://learn.tx2.qrqy.net/api/product/getListByCon?page=", // 
      saveY: 0, // Y
      lockload: true //
    };
  },
  components: {
    tabbar,
    search,
    swipe,
    gird,
    backtop,
    recommend,
    firstnew,
    goodslist,
    scroll,
    goodsinfo
  },
  methods: {
    // 
    handleScroll() {
      if (this.lockload) {
        var that = this;
        var scrollTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        var windowHeight =
          document.documentElement.clientHeight || document.body.clientHeight;
        var scrollHeight =
          document.documentElement.scrollHeight || document.body.scrollHeight;
        if (scrollTop + windowHeight == scrollHeight) {
          // ()
          // var min = 1;
          // var max = 35;
          // this.currentpage = Math.floor(Math.random() * (max - min + 1)) + min;
          if (that.currentpage >= 35) {
            alert("");
          } else {
            // +1
            that.currentpage++;
            // goodslist.vue
            that.$refs.goodslist.getgoods(that.pageaddress + that.currentpage);
          }
        }
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    console.log("");
    window.removeEventListener("scroll", this.handleScroll);
  },
  activated() {
    this.$refs.scroll.scrollTo(0, this.saveY);
    this.$refs.scroll.refresh();
    this.lockload = true;
  },
  deactivated() {
    this.saveY = this.$refs.scroll.scroll.y;
    this.lockload = false;
  }
};
</script>

<style scoped>
.swipewrap {
  position: relative;
  margin-top: 37px;
}
.cover {
  position: absolute;
  width: 100%;
  height: 10px;
  bottom: -0.026667rem;
  background-image: url("../assets/coverline.png");
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center center;
}
.recommend {
  margin: 10px auto;
  margin-bottom: 0;
}
.youprefer {
  display: block;
  box-sizing: border-box;
  width: 100%;
  height: 1.253333rem;
  text-align: center;
  padding: 0.32rem 0;
}
.youpreferimg {
  width: 4.293333rem;
  height: 0.506667rem;
  vertical-align: top;
}
.goodslistwrap {
  width: 10rem;

  position: relative;
  box-sizing: border-box;
  padding: 0 0.2rem;
  background: #f2f2f2;
  padding-bottom: 0.32rem;
}
.backtop {
  z-index: 20;
  position: fixed;
  bottom: 70px;
  cursor: pointer;
  transition: opacity 0.2s linear 0s, visibility;
  z-index: 1000;
  opacity: 1;
  visibility: visible;
}
</style>



