<template>
  <!-- () -->
  <div class="goodsbg">
    <!--  -->
    <div class="imgswrap">
      <van-swipe :loop="false" @change="onChange">
        <van-swipe-item v-for="(val,inx) in imglist" :key="inx">
          <img class="goodsinfoimg" :src="val" @click="imgpreview()" />
        </van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">{{ current + 1 }}/{{imgnum}}</div>
        </template>
      </van-swipe>
    </div>

    <!--  +   -->
    <div class="topbar">
      <div class="back iconfont icon-back" @click="backbutton"></div>
      <div class="tocart iconfont icon-cart" @click="tocart"></div>
    </div>

    <!--  -->
    <div>
      <goodstitle class="goodsinfocenter" :message="goodsmessage"></goodstitle>
      <!-- goodsinfoblock10px -->
      <div class="goodsinfoblock"></div>
      <bottompop
        :title="this.parameters.serve[0]"
        :description="this.parameters.serve[1]"
        :img="this.parameters.serve[2]"
      ></bottompop>
      <div class="goodsinfoblock"></div>
      <bottompop :title="this.parameters.guige[0]" :description="this.parameters.guige[1]"></bottompop>
      <bottompop :title="this.parameters.canshu[0]" :description="this.parameters.canshu[1]"></bottompop>
      <div class="goodsinfoblock"></div>
    </div>

    <!--  +   -->
    <reviews></reviews>

    <!--  -->
    <div>
      <!--  -->
      <modulestart :whichmodule="this.module.goodsinfo"></modulestart>
      <div class="force"></div>
      <!--  -->
      <img class="goodsinfoimg" v-for="(val,inx) in imglist" :key="inx" :src="val" />
    </div>

    <!--  -->
    <div>
      <!--  -->
      <modulestart :whichmodule="this.module.goodsrecommend"></modulestart>
      <!--  -->
      <goodslist class="goodslistwrap2 clearfix"></goodslist>
    </div>

    <!-- tabbar -->
    <goodsaction></goodsaction>
  </div>
</template>


<script>
import modulestart from "./modulestart";
import goodslist from "./goodslist";
import reviews from "./reviews";
import bottompop from "./bottompop";
import goodstitle from "./goodstitle";
import { ImagePreview } from "vant";
import goodsaction from "./goodsaction";
export default {
  name: "goodsinfo",
  data() {
    return {
      current: 0, //index
      id: 0, //id
      goodsmessage: {}, //
      imglist: [], //
      imgnum: 0, //
      parameters: {
        serve: ["", "7", "@a/fakeimg/serve.png"],
        guige: ["", ""],
        canshu: ["", "   .."]
      },
      module: {
        goodsinfo: "",
        goodsrecommend: ""
      },
      // 
      pageaddress: "http://learn.tx2.qrqy.net/api/product/getListByCon?page="
    };
  },
  methods: {
    // iddata
    getid() {
      this.id = this.$route.params && this.$route.params.id;
    },
    // 
    onChange(index) {
      this.current = index;
    },
    // Vant
    imgpreview() {
      ImagePreview(this.imglist);
    },
    // 
    backbutton() {
      // 
      this.$router.back(-1);
      this.$router.back(-1);
    },
    //   
    tocart() {
      this.$router.push({ name: "cart" });
    }
  },

  components: {
    goodsaction,
    goodstitle,
    bottompop,
    reviews,
    modulestart,
    goodslist
  },
  mounted() {
    // id
    this.getid();
    // 
    this.$http
      .get("http://learn.tx2.qrqy.net/api/product/getById?id=" + this.id)
      .then(res => {
        // id
        this.goodsmessage = res.data.ret;
        // imglist
        this.imglist = res.data.ret.img_arr;
        this.imgnum = this.imglist.length;
      })
      .catch(e => {
        console.log(e);
      });
  }
};
</script>


<style>
.custom-indicator {
  position: absolute;
  display: inline-block;
  right: 0.266667rem;
  bottom: 0.666667rem;
  font-size: 0.266667rem;
  height: 0.533333rem;
  line-height: 0.573333rem;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 0 0.266667rem;
  border-radius: 0.533333rem;
  z-index: 10;
}
.goodsbg {
  position: relative;
  background-color: #fff;
}
.van-swipe__track {
  transform: translateX(-175px);
}
.imgswrap {
  /* height: 375px; */
  width: 100%;
  text-align: center;
}
.van-swipe {
  width: 100%;
  /* height: 3.2rem; */
  text-align: center;
}
.topbar {
  position: fixed;
  z-index: 10;
  width: 10rem;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 0 10px;
  box-sizing: border-box;
}
.back,
.tocart {
  position: relative;

  float: left;
  height: 34px;
  width: 34px;
  border-radius: 50%;
  text-align: center;
  background: rgba(0, 0, 0, 0.4);
  box-sizing: border-box;
  color: #fff;
  padding-top: 6px;
}
.tocart {
  float: right;
}
.icon-back:before,
.icon-cart:before {
  font-size: 22px;
  color: #fff;
  top: 6px;
}
.goodsinfocenter {
  width: 100%;
  margin: 0 auto;
}
.goodsinfoblock {
  width: 10rem;
  height: 0.266667rem;
  background: #f4f4f4;
}
.blockinfo {
  height: 500px;
}
.divide-bar {
  background-color: #f2f2f2;
  color: #999;
  padding: 0.213333rem 0;
  display: flex;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
}
.line {
  display: inline-block;
  width: 1.066667rem;
  border-top: 0.026667rem solid #999;
}
.icon-info {
  position: relative;
  top: -0.026667rem;
  margin: 0 0.213333rem;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
}
.icon-piclight:before {
  font-size: 0.48rem;
}
.icon-text {
  font-size: 0.32rem;
  margin-left: 0.026667rem;
}
.goodsinfoimg {
  width: 10rem;
  height: 10rem;
  object-fit: cover;
}
.goodslistwrap2 {
  width: 10rem;
  position: relative;
  box-sizing: border-box;
  padding: 0 0.2rem;
  background: #ffffff;
  padding-bottom: 0.32rem;
  margin-bottom: 55px;
}
.force {
  text-align: center;
  font-size: 0.74rem;
}
</style>




